'use client';
import React, { useEffect, useState, useMemo } from 'react';
// FIX: Change absolute paths to relative paths
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';
import { Award, CheckCircle, Search, ChevronDown, Loader2 } from 'lucide-react';

interface ResultUser {
    username: string;
    domain: string;
    round: number | string; // Allow string for "Champions" later if needed
}

export default function ResultsPage() {
    const [results, setResults] = useState<ResultUser[]>([]);
    const [loading, setLoading] = useState(true);
    
    // --- NEW FILTER STATES ---
    const [selectedRound, setSelectedRound] = useState('All');
    const [availableRounds, setAvailableRounds] = useState<string[]>([]);
    // --- END NEW FILTER STATES ---

    const fetchResults = async () => {
        setLoading(true);
        try {
            const res = await fetch('/api/results');
            const data = await res.json();
            
            if (data.success) {
                setResults(data.results);
                
                // --- PROCESS AVAILABLE ROUNDS ---
                const rounds = new Set<string>();
                data.results.forEach((user: ResultUser) => {
                    // Check that the round exists and is a positive number/string
                    if (user.round && user.round > 0) {
                        rounds.add(`Round ${user.round}`);
                    }
                    // Optional: Add logic for a "Champion" string if round is assigned a special status
                    if (user.round === 'Champion') {
                         rounds.add('Champion');
                    }
                });
                
                const sortedRounds = Array.from(rounds).sort((a, b) => {
                    // Handle "Round X" format for sorting
                    const roundA = a.startsWith('Round') ? parseInt(a.split(' ')[1]) : 0;
                    const roundB = b.startsWith('Round') ? parseInt(b.split(' ')[1]) : 0;
                    
                    if (a === 'Champion') return -1; // Place Champion first
                    if (b === 'Champion') return 1;
                    
                    return roundB - roundA; // Descending order (Latest round first)
                });

                setAvailableRounds(['All', ...sortedRounds]);
                // Set initial filter to the latest round or 'All'
                setSelectedRound(sortedRounds[0] || 'All');
            }
        } catch (error) {
            console.error("Failed to fetch results", error);
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchResults();
    }, []);

    // Grouping and Filtering Logic
    const groupedResults = useMemo(() => {
        const roundToFilter = selectedRound === 'All' ? null : selectedRound;

        const filtered = results.filter(user => {
            // Determine the key for the user's round (e.g., "Round 2" or "Champion")
            const userRoundKey = user.round ? (typeof user.round === 'number' ? `Round ${user.round}` : user.round) : null;
            
            // If filtering by a specific round, check for a match
            if (roundToFilter && userRoundKey !== roundToFilter) {
                return false;
            }
            return true;
        });

        // Group the filtered results by round key
        return filtered.reduce((acc, user) => {
            const key = user.round ? (typeof user.round === 'number' ? `Round ${user.round}` : user.round) : 'Unassigned';
            if (!acc[key]) {
                acc[key] = [];
            }
            acc[key].push(user);
            return acc;
        }, {} as Record<string, ResultUser[]>);

    }, [results, selectedRound]);

    // Use the availableRounds for sorting and display order
    const sortedRounds = useMemo(() => {
        const roundKeysInResults = Object.keys(groupedResults);
        
        // If 'All' is selected, filter availableRounds to match keys present in results
        if (selectedRound === 'All') {
             return availableRounds.filter(r => r !== 'All' && roundKeysInResults.includes(r));
        }
        
        // Otherwise, just show the single selected round key
        return roundKeysInResults.filter(key => key === selectedRound);

    }, [groupedResults, selectedRound, availableRounds]);


    return (
        <div className="min-h-screen bg-[#050505] text-white font-sans pt-24">
            <Navbar user={null} onOpenAuth={() => {}} onLogout={() => {}} onOpenAdmin={() => {}} />

            <div className="max-w-7xl mx-auto px-6 py-16">
                
                <header className="text-center mb-16">
                    <Award className="w-12 h-12 text-[#ccff00] mx-auto mb-4" />
                    <h1 className="text-5xl md:text-7xl font-bold font-display tracking-tighter text-white">
                        Audition Results
                    </h1>
                    <p className="text-xl text-gray-400 mt-4">
                        Congratulations to all candidates selected in various rounds!
                    </p>
                </header>

                {/* --- FILTER SECTION --- */}
                <div className="max-w-3xl mx-auto mb-10">
                    <div className="relative flex items-center justify-between bg-[#111] border border-white/10 rounded-xl p-3 md:p-4">
                        <label className="text-sm text-gray-400 font-medium mr-4 hidden sm:block">Filter by Round:</label>
                        
                        <div className="relative flex-1">
                            <select
                                value={selectedRound}
                                onChange={(e) => setSelectedRound(e.target.value)}
                                className="appearance-none w-full bg-transparent text-white py-1.5 pl-3 pr-8 focus:outline-none text-base cursor-pointer"
                                disabled={loading || availableRounds.length <= 1}
                            >
                                {availableRounds.map(round => (
                                    <option key={round} value={round} className="bg-black text-white p-2">
                                        {round}
                                    </option>
                                ))}
                            </select>
                            <ChevronDown className="absolute right-2 top-1/2 -translate-y-1/2 w-4 h-4 text-[#ccff00] pointer-events-none" />
                        </div>
                    </div>
                </div>
                {/* --- END FILTER SECTION --- */}

                {loading ? (
                    <div className="text-center p-20 text-gray-500 flex flex-col items-center">
                        <Loader2 className="w-6 h-6 animate-spin text-[#ccff00] mb-3" />
                        Fetching latest results...
                    </div>
                ) : (
                    sortedRounds.length > 0 ? (
                        <div className="space-y-12">
                            {sortedRounds.map((roundKey) => {
                                const usersInRound = groupedResults[roundKey] || [];
                                
                                return usersInRound.length > 0 ? (
                                    <section key={roundKey} className="bg-[#0a0a0a] p-6 md:p-10 rounded-2xl border border-white/10 shadow-xl">
                                        <h2 className="text-3xl font-bold font-display mb-6 border-b border-[#ccff00]/50 pb-2 text-[#ccff00]">
                                            {roundKey} ({usersInRound.length} selected)
                                        </h2>

                                        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
                                            {usersInRound.map((user, userIndex) => (
                                                <div key={userIndex} className="bg-[#111] p-4 rounded-lg flex flex-col justify-between border border-white/5 hover:border-[#ccff00]/30 transition-colors duration-300">
                                                    <div className="flex items-center gap-2 mb-2">
                                                        <CheckCircle className="w-4 h-4 text-green-400 shrink-0" />
                                                        <span className="font-semibold text-white text-base truncate">{user.username}</span>
                                                    </div>
                                                    <div className="text-xs text-gray-500 uppercase tracking-wider">
                                                        Domain
                                                    </div>
                                                    <div className="font-bold text-sm text-gray-300 capitalize">
                                                        {user.domain.replace(/-/g, ' ')}
                                                    </div>
                                                </div>
                                            ))}
                                        </div>
                                    </section>
                                ) : null;
                            })}
                        </div>
                    ) : (
                        <div className="text-center p-20 text-gray-500 border border-white/10 rounded-xl max-w-3xl mx-auto">
                            No final results have been published yet, or the selected round has no results.
                        </div>
                    )
                )}
            </div>
            
            <Footer />
        </div>
    );
}